@font-face {
    font-family: 'Michelle Handwriting';
    src: url('michellehandwritting.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

* {
    /* font-family: 'Michelle Handwriting', sans-serif;
    font-size: 1.1rem; */
}

html, body {
    min-height: 100%;
}

#root, .App {
    min-height: 100%;
}

body {
    background-color: #fff;
    /* Slightly lighter grey for the page background */
}

nav {
    display: flex;
    justify-content: center;
    gap: 20px;
    padding: 20px 0 40px;
}

nav span {
    cursor: pointer;
    font-size: 1.6rem;
}

nav span.active {
    text-decoration: underline;
}

#choices {
    display: flex;
    gap: 18px;
    padding: 20px 0;
    width: 100%;
    box-sizing: border-box;
    overflow: auto;
}

#choices img {
    height: 155px;
    width: auto;
    cursor: pointer;
    border-radius: 4px;
}

/* General layout and tool container */
#tool {
    overflow: hidden;
    box-sizing: border-box;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 1100px;
    margin: 0 auto;
    padding: 20px;
}

/* Container for centering the tool */
#container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

/* Builder section */
#builder {
    padding: 0 0 10px;
    display: flex;
    flex-direction: column;
    gap: 3px;
    box-sizing: border-box;
    flex-direction: row;
    gap: 15px;
    box-sizing: border-box;
    width: 100%;
}

#options {
    padding: 10px;
}

#options div {
    display: flex;
    align-items: center;
}

#options label {
    margin-right: 10px;
}

#options #link {
    margin-top: 15px;
}

#link a {
    text-decoration: none;
    padding: 6px 12px 7px;
    border: 1px solid #838383;
    background: #e5e5e5;
    color: #000;
    margin-top: 18px;
    display: inline-block;
}

#link a:hover {
    background: #e7e7e7;
    border-color: #a2a2a2;
}

/* Input text areas */
#builder textarea {
    border: 1px solid #bfbfbf;
    color: #000000;
    padding: 5px;
    width: 100%;
    box-sizing: border-box;
    resize: vertical;
    height: 55px;
    width: 100%;
}

#builder #textInputs {
    flex: 1;
}

.asset {
    width: 100px;
    height: 100px;
    border: 1px solid #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    flex-direction: column;
    gap: 6px;
    border-radius: 6px;
    opacity: 0.6;
}

.asset.asset-image {
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.asset:hover {
    border-color: #909090;
    opacity: 1;
}

.text-line {
    margin-bottom: 15px;
}

.text-line .label {
    margin-bottom: 8px;
    font-weight: bold;
    font-size: 1.1rem;
}

.text-line .label label {
    margin: 0 8px 0 0;
}

/* Logo styling */
img#logo {
    display: block;
    height: 57px;
    width: auto;
    margin: 10px auto 0;
}

/* Canvas styling */
#tool canvas {
    border: 1px solid rgb(168 168 168) !important;
}

.layer-row {
    padding: 3px 7px;
}

.layer-row input[type="text"] {
    font-size: 1rem;
}

.layer-row.selected {
    background: #d3e7ff;
}

/* Container for the tabs */
.tabs-container {
    display: flex;
    border-bottom: 2px solid #ccc;
    margin-bottom: 10px;
}

/* Individual tab button styles */
.tab-button {
    padding: 5px 6px;
    cursor: pointer;
    border: none;
    background: none;
    outline: none;
    transition: background-color 0.3s;
    font-weight: bold;
    font-size: 18px;
    margin-right: 8px;
    border-bottom: 3px solid transparent;
}

/* Active tab styling */
.tab-button.active {
    border-bottom-color: #007BFF;
    color: #007BFF;
}

/* Tab button hover effect */
.tab-button:hover {
    background-color: #f0f0f0;
}

/* Container for tab content */
.tab-content {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
}


/* Responsive design */
@media (max-width: 1100px) {
    #builder {
        flex-direction: column;
    }

    #tool {
        width: 640px;
        justify-content: flex-start;
    }

    #choices {
        overflow: visible;
        flex-wrap: wrap;
    }
}

@media (max-width: 600px) {
    #tool {
        width: auto;
    }

    .layer-row input[type="text"] {
        width: 145px;
    }
}